
import { SET_NOTIFICATION_GRANTED } from "@/store/modules/settings";
import { defineComponent } from "@vue/runtime-core";

export default defineComponent({
  mounted() {
    this.$store.commit(SET_NOTIFICATION_GRANTED);
  },
  computed: {
    desc() {
      return this.$store.state.settings.notificationsGranted
        ? this.$t.grantedNotificationsDesc
        : this.$t.enableNotificationsDesc;
    },
  },
});
